body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  min-height: 100vh;
}

.app {
  max-width: 1000px;
  margin: auto;
  padding: 40px;
  color: white;
}

.header {
  text-align: center;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 36px;
  margin: 0;
  font-weight: 700;
}

.header p {
  opacity: 0.7;
  margin-top: 8px;
}

.card {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(15px);
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.4);
}

.input-section {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

label {
  font-weight: 600;
  font-size: 14px;
  opacity: 0.8;
}

input,
textarea {
  padding: 14px;
  border-radius: 12px;
  border: none;
  outline: none;
  font-size: 14px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

textarea {
  min-height: 120px;
  resize: none;
}

button {
  padding: 14px;
  border-radius: 12px;
  border: none;
  font-size: 16px;
  cursor: pointer;
  background: linear-gradient(135deg, #00c6ff, #0072ff);
  color: white;
  font-weight: 600;
  transition: 0.3s ease;
}

button:hover {
  transform: scale(1.03);
  box-shadow: 0px 10px 25px rgba(0, 114, 255, 0.6);
}

.result-section {
  margin-top: 40px;
}

.score-wrapper {
  display: flex;
  gap: 40px;
  justify-content: center;
  margin-bottom: 30px;
}

.score-box {
  text-align: center;
}

.score-circle {
  width: 130px;
  height: 130px;
  border-radius: 50%;
  background: linear-gradient(135deg, #00f2fe, #4facfe);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
  font-weight: bold;
  margin-top: 10px;
}

.score-circle.ats {
  background: linear-gradient(135deg, #2ecc71, #27ae60);
}

.bar-container {
  height: 8px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  margin-bottom: 30px;
}

.bar {
  height: 100%;
  background: linear-gradient(135deg, #00f2fe, #4facfe);
  border-radius: 6px;
  transition: width 0.6s ease;
}

.skills-container {
  display: flex;
  gap: 40px;
  margin-top: 20px;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tag {
  padding: 8px 14px;
  border-radius: 30px;
  font-size: 13px;
  font-weight: 500;
}

.good {
  background: rgba(46, 204, 113, 0.2);
  color: #2ecc71;
  border: 1px solid #2ecc71;
}

.bad {
  background: rgba(231, 76, 60, 0.2);
  color: #e74c3c;
  border: 1px solid #e74c3c;
}

.extra-section {
  margin-top: 40px;
}

.suggestion-card {
  background: rgba(255, 255, 255, 0.07);
  padding: 15px;
  border-radius: 12px;
  margin-bottom: 15px;
}

.suggestion-card h4 {
  margin: 0 0 10px 0;
  color: #00f2fe;
}

.suggestion-card ul {
  padding-left: 20px;
  margin: 0;
}

.suggestion-card li {
  margin-bottom: 5px;
  font-size: 14px;
}
